@import '../App/App.scss';

header {
  border: 5px solid $black-green;
  display: flex;
  justify-content: space-around;
  background: $medium-green;
  background: linear-gradient( #00b40080, #00b40080), url("data:image/svg+xml;base64,");
  padding: 2vh;
  margin-block-end: 4vh;
  button {
    color: #000000;
    background-color: #D6C73C;
    font-size: 30px;
    width: auto;
  }
}

.active {
  button {
    background-color: #726a20;
  }
}

h1 {
  font-size: 70px;
  margin-block-start: 0em;
  margin-block-end: 0em;
  align-self: center;
  font-weight: 900;
  -webkit-text-stroke: .1px black;
  color: white;
}

.reptile-image {
  button {
    height: 10vh;
  }
  img {
    height: 21vh;
    width: 12vw;
  }
  display: flex;
  justify-content: space-evenly;
}

.button-and-image {
  display: flex;
  flex-flow: column;
}